
import { defineComponent } from "vue";
import onClickOutside from "@/directives/OnClickOutside.js";
import {
  ChevronDownIcon,
  CalendarDaysIcon,
  XMarkIcon,
} from "@heroicons/vue/20/solid";
import FilterFormModal from "@/components/FilterFormModal.vue";
import { mapMutations, mapActions } from "vuex";

export default defineComponent({
  name: "DropDownMenu",
  props: { coin_id: String },
  directives: {
    onClickOutside,
  },
  components: { ChevronDownIcon, CalendarDaysIcon, XMarkIcon, FilterFormModal },
  data() {
    return {
      visible: false,
      open: false,
    };
  },
  methods: {
    ...mapMutations(["removeFilter"]),
    ...mapActions(["catchCoinValue"]),

    toogleMenu() {
      this.visible = !this.visible;
    },
    openMenu() {
      this.visible = true;
    },
    closeMenu() {
      if (this.visible && !this.open) {
        this.open = true;
      } else if (this.visible && this.open) {
        this.visible = false;
        this.open = false;
      }
    },
    clearFilters() {
      this.removeFilter(this.coin_id);
      this.catchCoinValue();
      this.closeMenu();
    },
  },
});
